<template>
  <div class="index-view">
     <el-table
      border
      :data="fundsData">
      <el-table-column align="center" prop="name" label="基金名称" width="120"> </el-table-column>
      <el-table-column align="center" label="初始资产" width="100">
        <template slot-scope="scope">
          <span class='profit'>{{scope.row.init_val}}{{scope.row.coin}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="收益" width="120">
        <template slot-scope="scope">
          <span class='profit'>{{(scope.row.profits[scope.row.profits.length - 1] * scope.row.init_val / 100).toFixed(2) }} {{scope.row.coin}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="收益率" width="90">
        <template slot-scope="scope">
          <span class='profit'>{{scope.row.profits[scope.row.profits.length - 1]}}%</span>
        </template>
      </el-table-column>
<!--       <el-table-column align="center" label="估算月化" width="90">
        <template slot-scope="scope">
          <span class='profit'>{{(dayProfit(scope.row) * 30).toFixed(2)}}%</span>
        </template>
      </el-table-column> -->
      <el-table-column align="center" label="估算年化" width="90">
        <template slot-scope="scope">
          <span class='profit'>{{(dayProfit(scope.row) * 365).toFixed(2)}}%</span>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="begin" label="开始时间" width="120"> </el-table-column>
      <el-table-column align="center" prop="end" label="预计结束时间" width="120"> </el-table-column>
      <el-table-column align="center" label="实盘天数" width="110">
        <template slot-scope="scope">
          <span>{{scope.row.profits.length}}天</span>
          <span v-if='isEnd(scope.row)'>,已结束</span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作" width>
        <template slot-scope="scope">
          <el-button type="text" size="small" @click='checkGraph(scope.row)'>查看收益走势</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="提示"
      class='dialog'
      :visible.sync="dialogVisible"
      :before-close="handleClose">
      <div id="profit-chart"></div>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'IndexView',
  data () {
    return {
      dialogVisible: false,
      fundsData: [],
      fund: {},
    }
  },
  methods: {
    isEnd (row) {
      return new Date(row.end).getTime() < new Date().getTime()
    },
    dayProfit (row) {
      let day = row.profits.length
      let last = row.profits[row.profits.length - 1]
      return (last/ day).toFixed(4)
    },
    checkGraph(row) {
      this.dialogVisible = !this.dialogVisible
      if (row.name == this.fund.name) {
        return
      }
      this.fund = row
      let begin = new Date(row.begin).getTime()
      let data = row.profits.map((el, index) => {
        return [begin + 1000*60*60*24 * index, row.profits[index]]
      })
      setTimeout(() => {
        window.Highcharts.chart('profit-chart', {
        'tooltip': {},
        'title' : {'text' : `${this.fund.name}收益走势`},
        chart: {
          type: 'line'
        },
        navigator: {
          enabled: true
        },
        legend: {
            enabled: true,
            layout: 'horizontal'
        },
        rangeSelector: {
          'enabled': true,
          'buttons':  [
            {'type': 'week', 'count': 1, 'text': '1周'},
            {'type': 'week', 'count': 2, 'text': '2周'},
            {'type': 'week', 'count': 3, 'text': '3周'},
            {'type': 'week', 'count': 4, 'text': '4周'},
            {'type': 'all','text': '全部'}
          ],
          'selected': 5,
          'inputEnabled': false
        },
        xAxis: {
          type: 'datetime',
        },
        'yAxis' : {
          'title': {'text': ''},
          'opposite': false,
        },
        series: [
          {
            showInLegend: true,
            name: '收益率(%)',
            data: data,
            'tooltip': {
              'valueDecimals': 2,
            }
          }
        ]
      });
      document.querySelector('.highcharts-credits').style.display = 'none'
      }, 100)
    },
    handleClose(done) {
      this.dialogVisible = false
    }
  },
  mounted () {
    [
      'https://easy-mock.com/mock/5cde828b6f52db23ef85edd1/wbfund/eric',
      'https://easy-mock.com/mock/5cde828b6f52db23ef85edd1/wbfund/lee',
    ].forEach(url => {
      axios.get(url).then(res => {
        this.fundsData = this.fundsData.concat(res)
      }).catch((err) => {
         console.log(err)
      });
    })
  }
}
</script>

